import axios from 'axios';
import {
    klaytnAPIToken,
    factoryAddress,
    PoPoContractAddress,
    forMoveFactoryAddress,
} from '@/helpers/_common.linkChain';

import dotenv from 'dotenv';
dotenv.config();

const baseURL = 'https://th-api.klaytnapi.com/v2';

const contractAddresses: string[] = [PoPoContractAddress, '0x86b0f3dfddadb2e57b00a6d740f1a464f79179bf'];

export const getMyTokens_in_kaikas = async (walletAddress: string, cursor?) => {
    if (cursor) {
        return await axios.get(
            `${baseURL}/account/${walletAddress}/token?kind=nft&ca-filters=${contractAddresses.join(
                ',',
            )}&size=1000&cursor=${cursor}`,
            {
                headers: {
                    'X-Chain-Id': '8217',
                    'Authorization': klaytnAPIToken,
                },
            },
        );
    } else {
        return await axios.get(
            `${baseURL}/account/${walletAddress}/token?kind=nft&ca-filters=${contractAddresses.join(',')}&size=1000`,
            {
                headers: {
                    'X-Chain-Id': '8217',
                    'Authorization': klaytnAPIToken,
                },
            },
        );
    }
};

